body{
  background-color: black;
}

.Navbar{
  width: 100%;
  background-color: #0e0101e1;
  display: flex;
  position: fixed;

}
.name {
 color: whitesmoke; 
 margin-left: 20px;
}

.Navbar-link{
  display: flex;
  text-align: left;
  margin-top: 10px;
  margin-left: 40%;
}
.Navbar, a:hover{
  background-color:#5a24ce;
  color: aliceblue;
  font-size: larger;
 
}
.Navbar-link ul,li,a{
  color: white;
 padding-right: 20px;
text-decoration: none;
  list-style: none;
  
}


.Home{
  text-align: left;
  margin-right: 30%;
  display: inline-block;
  
}

.intro{
  width: 100%;
  height: 80vh;
  text-align: left;
  color: beige;
  background-color: #0e0101e1;
 display: flex;
 

  
}
.intro2{
  width: 50%;
  height: 40vh;
  margin-left: 8%;
  padding-top: 5%;
 margin-top: 5%;
  
}
.summary{
  font-size: medium;
  font-style: italic;
  margin-top: 30px;
  line-height: 30px;
}
.image-me{
  width: 300px;
  height: 40vh;
  border: 2px solid yellow;
  border-radius: 50%;
  margin-top: 10%;
  margin-left: 90px;

 
}

.btn{
margin-left: 15px;
  
}
.about-me{
  width: 100%;

  background-color: #15b9e7;
}

.about,.about-me1{
  width: 80%;
  height: 60vh;
  text-align: left;
  margin-left: 10%;
  line-height: 65px;
  position: relative;
  animation: mymove 2s;
  animation-fill-mode: forwards;
}
@keyframes mymove {
  from {left: 0px;}
  to {left: 100px;}
  
}
#about2 ,.btn{
 width: 30%;
 margin-top: 20px;
  
}
.skill-parent{
  width: 100%;

}
.skills {
  margin-top: 10px;
  width: 50%;
  text-align: center;
  padding-top: 5px;
  padding-bottom: 10px;
  color: white;
 
  border-radius: 5px;
 
}


.html {width: 90%; background-color: #04AA6D;}
.css {width: 80%; background-color: #2196F3;}
.js {width: 65%; background-color: #f44336;}
.ReactJS {width: 60%; background-color: #5a24ce;}
.NodeJS {width: 55%; background-color: #df660f;}

.chapter,p{
 
 margin-right: 80%;
 margin-top: 10%;
 font-size: xx-large;


}
.my-skills{
  font-size: XX-large;
  width: 40%;
  
  
}
.con{
  text-align: center;
  
}
.contact{
  margin-top: 3%;
  margin-left:15%;
  display: inline-block;
}

.contact ,input{
  width: 50%;
  color: whitesmoke;
  border-radius: 5px;
  padding: 5px;
}
.mobile{
  margin-top: 30px;
}
.icons1,.icons2,.icons3{
  display: inline-block;
  width: 15%;
  font-size: xx-large;
 
  
}
